import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  useSelector,
  useDispatch,
} from "react-redux"; /* 2 hook get, push d liu ln store Redux */
import { Modal, Button, ModalBody, FormGroup, Label, Input } from "reactstrap";
import "../App.css";
import {postContact} from "../redux/reducer"


import { setStatecontact, contactReducerThunk } from "../redux/reducer"; // import actions t reducer(cng nhn)

const StaffListComponent = ({ selectStaff }) => {
  /* to bin dispatch */
  const dispatch = useDispatch();
  ///////////////////////////////////
  /* get data on use Input */
  const [dataID, setdataID] = useState("");
  const [dataName, setdataName] = useState("");
  const [dataBirth, setdataBirth] = useState("");
  const [dataInjob, setdataInjob] = useState("");
  const [dataSelect, setdataSelect] = useState("");
  const [dataSalary, setdataSalary] = useState("");
  const [dataDaysOff, setdataDaysOff] = useState("");
  const [dataOverTime, setdataOverTime] = useState("");
  const [dataSalaryPayment, setdataSalaryPayment] = useState("");
  //get data ID
  const getdataID = (event) => setdataID(event.target.value);
  // get data Name
  const getdataName = (event) => setdataName(event.target.value);
  //get data date birth
  const getdataBirth = (event) => setdataBirth(event.target.value);
  //get data in job
  const getdataInjob = (event) => setdataInjob(event.target.value);
  // get data select
  const getdataSelect = (event) => setdataSelect(event.target.value);
  //get data Salary
  const getdataSalary = (event) => setdataSalary(event.target.value);
  //get data days off
  const getdataDaysOff = (event) => setdataDaysOff(event.target.value);
  // get data overTime
  const getdataOvertime = (event) => setdataOverTime(event.target.value);
  // get data SalaryPay
  /* const getdataSalaryPay = (event) => setdataSalaryPayment(event.target.value); */

  ///////////////////////////////////
  /* state toggle */
  const [modal, setModal] = useState(false);
  const toggleModal = () => {
    // set li data ca input khi thot khi modal
    setdataID("");
    setdataName("");
    setdataBirth("");
    setdataInjob("");
    setFormblur(false);
    /* setdataSalaryPayment(""); */
    // toggle modal
    setModal(!modal);
  };
  ///////////////////////////////////
  /* handleADD */
  const handleADD = () => {
    const stateContact = {
      id: dataID,
      name: dataName,
      doB: dataBirth,
      salaryScale: dataSalary,
      startDate: dataInjob,
      department: dataSelect,
      annualLeave: dataDaysOff,
      overTime: dataOverTime,
      image: "https://www.cse.ust.hk/~muppala/img/muppala.jpg",
      /* salary: dataSalaryPayment, */
    };
    if (
      // so snh iu kin input nhp vo nu nhp  cc trng th cho php dispatch ln store.
      stateContact.id != "" &&
      stateContact.name != "" &&
      stateContact.doB != "" &&
      stateContact.startDate != ""
    ) {
      dispatch(postContact(stateContact));
      console.log("thanhcong")
      toggleModal();
    } else AddBlur(); // gi trc tip t validate form
  };
  ///////////////////////////////////
  /* state option select */ // ly t staffs
  /* Data Contact and search and options */
  //get data contact
  const dataContact = useSelector((data) => data.dataContact.contact);
  const [contact, setcontact] = useState(dataContact);
  useEffect(() => {
    setcontact(dataContact);
    console.log("load",contact);
  }, [dataContact]);

  // Options Selector
  const dataDepartments = useSelector((data) => {
    return data.dataContact.departments;
  });
  const [option, setOptions] = useState(() => {
    return dataDepartments.map((depart) => {
      return depart.name;
    });
  });



  // to state  lu tr gi tr input search
  const [dataSearch, setdataSearch] = useState("");
  const getdataSearch = (event) => {
    setdataSearch(event.target.value);
  };
  /* Button search onclick */
  const btSearch = () => {
    // to 1 bin mi  lu dataStaffs get t store
    const listFilter = dataContact;
    // filter lc  mng vi iu kin ch nh,nu tha iu kin s to ra cc phn t  lc thnh mng con
    // includes kim tra 1 iu kin u vo c nm trong 1 mng, nu ng tr v true
    const newListStaff = listFilter.filter((contact) => {
      return contact.name.toLowerCase().includes(dataSearch.toLowerCase());
    });
    setcontact(newListStaff); //set state staffs l 1 d liu mi  lc
  };

  ///////////////////////////////////
  /* Validate form */
  // to state cho validate form
  const [formblur, setFormblur] = useState(false);
  // hin th Blur validate form c gi  dng 121 khi button add c onclick
  const AddBlur = () => {
    setFormblur(true);
  };
  // so snh iu kin input
  const HandleBlurID = () => {
    if (formblur !== false && dataID == "") {
      return <div>Yu Cu nhp</div>;
    } else return <div></div>;
  };
  const HandleBlurName = () => {
    if (formblur !== false && dataName == "") {
      return <div>Yu Cu nhp</div>;
    } else return <div></div>;
  };
  const HandleBlurDatebirth = () => {
    if (formblur !== false && dataBirth == "") {
      return <div>Yu Cu nhp</div>;
    } else return <div></div>;
  };
  const HandleBlurDatestart = () => {
    if (formblur !== false && dataInjob == "") {
      return <div>Yu Cu nhp</div>;
    } else return <div></div>;
  };

  ///////////////////////////////////

  /* to bin render */
  const Render = () => {
    // to bin uidataState
    const uidataState = (contact) => (
      <div className="col-lg-2 col-md-4 col-sm-6 staff" key={contact.id}>
        <Link to={`/contact/${contact.id}`}>
          <div
            onClick={() => {
              // lng nghe s kin onclick truyn d liu vo function selectStaff  main
              selectStaff(contact);
            }}
          >
            <img className="img" src={contact.image} alt={contact.name} />
            <div className="name">{contact.name}</div>
          </div>
        </Link>
      </div>
    );
    return contact.map(uidataState); // ly bin staffs t state data search
  };

  ///////////////////////////////////
  return (
    // s dng bootstraps  grid
    <div className="container">
      <h1 className="text-center">Nhn Vin</h1>
      <div className="d-flex">
        <Button
          style={{
            width: "70px",
            height: "70px",
            marginRight: "20px",
            marginTop: "20px",
            marginBottom: "20px",
          }}
          outline
          onClick={toggleModal}
        >
          <span className="fa fa-sign-in fa-lg login"></span> ADD
        </Button>
        <Input
          type="text"
          name="search"
          id="search"
          placeholder="Tm kim nhn vin"
          style={{ width: "30%", margin: "20px 0px" }}
          onChange={getdataSearch}
        />

        <Button
          style={{ width: "20%", height: "30%", marginTop: "20px", marginLeft: "20px" }}
          color="primary"
          onClick={btSearch}
        >
          Tm kim
        </Button>
      </div>

      {/* Modal */}
      <div className="row">
        <Modal isOpen={modal} toggle={toggleModal}>
          <ModalBody>
            <div className="d-flex">
              <h1>Thm nhn vin</h1>
              <Button onClick={toggleModal} className=" ms-auto h-75">
                <span className="fa-solid fa-xmark"> X </span>
              </Button>
            </div>
            <FormGroup>
              <Label htmlFor="Ten">M Nhn Vin:</Label>
              <Input type="number" name="id" id="id" onChange={getdataID} onBlur={HandleBlurID} />
              <div style={{ color: "red" }}>{HandleBlurID()}</div>
            </FormGroup>

            <FormGroup>
              <Label htmlFor="Ten">Tn:</Label>
              <Input
                type="text"
                name="Ten"
                id="Ten"
                onChange={getdataName}
                onBlur={HandleBlurName}
              />
              <div style={{ color: "red" }}>{HandleBlurName()}</div>
            </FormGroup>

            <FormGroup>
              <Label htmlFor="Ngaysinh">Ngy sinh:</Label>
              <Input
                type="date"
                name="Ngaysinh"
                id="Ngaysinh"
                onChange={getdataBirth}
                onBlur={HandleBlurDatebirth}
              />
              <div style={{ color: "red" }}>{HandleBlurDatebirth()}</div>
            </FormGroup>

            <FormGroup>
              <Label htmlFor="Ngayinjob">Ngy vo cng ty:</Label>
              <Input
                type="date"
                name="Ngayinjob"
                id="Ngayinjob"
                onChange={getdataInjob}
                onBlur={HandleBlurDatestart}
              />
              <div style={{ color: "red" }}>{HandleBlurDatestart()}</div>
            </FormGroup>

            {/* to select ti y */}
            <FormGroup>
              <Label htmlFor="PhongBan">Phng Ban:</Label>
              <Input id="PhongBan" onChange={getdataSelect} type="select">
                <option value={option[0]}>Sale</option>
                <option value={option[1]}>HR</option>
                <option value={option[2]}>Marketing</option>
                <option value={option[3]}>IT</option>
                <option value={option[4]}>Finance</option>
              </Input>
            </FormGroup>

            <FormGroup>
              <Label htmlFor="Hesoluong">H s lng:</Label>
              <Input type="number" name="Hesoluong" id="Hesoluong" onChange={getdataSalary} />
            </FormGroup>

            <FormGroup>
              <Label htmlFor="Songaynghiconlai">S ngy ngh cn li:</Label>
              <Input
                type="number"
                name="Songaynghiconlai"
                id="Songaynghiconlai"
                onChange={getdataDaysOff}
              />
            </FormGroup>

            <FormGroup>
              <Label htmlFor="Songaydalamthem">S ngy  lm thm:</Label>
              <Input
                type="number"
                name="Songaydalamthem"
                id="Songaydalamthem"
                onChange={getdataOvertime}
              />
            </FormGroup>

           {/*  <FormGroup>
              <Label htmlFor="Luongduoctra">Lng c tr:</Label>
              <Input
                type="number"
                name="Luongduoctra"
                id="Luongduoctra"
                onChange={getdataSalaryPay}
              />
            </FormGroup> */}
            <br></br>
            <Button type="submit" value="submit" color="primary" onClick={handleADD}>
              ADD
            </Button>
          </ModalBody>
        </Modal>
        <Render />
      </div>
    </div>
  );
};

export default StaffListComponent;
